body {
    margin: 0;
    color: #4B4B4B;
    font-family: source-sans-pro;
    font-style: normal;
    font-weight: 400;
    font-size: 1em;
    /* background-color: #EFF5F8; */
}
body {
    width: 100%;
    margin: 0 auto;
    background-color: Black;
}
h1 {
    font-style: normal;
    font-weight: 900;
}
h1 {
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 20px;
    padding-bottom: 20px;
    font-size: 2.5em;
    text-align: center;
    text-transform: uppercase;
}
h3 {
    margin-top: 1em;
    color:rgb(26, 57, 82)
}
#wrapper {
    background-color: black;
}
p {
    font-weight: 300;
    font-size: calc(0.75rem + 1vw);
}

.me a {
    font-weight:300;
    text-decoration: none;
    color: orange;
}
.me :hover, .me a:active, .me a:focus {
    color: deepskyblue;

}
h6 {
    font-weight: 300;
    text-align: center;
}

a:link {
    color: #10ebd2;
}
a:hover, a:active, a:focus {
    color: deepskyblue;
    text-decoration: underline;
}

#navlinks {
    width: 100%;
    list-style-type: none;
    padding: 0;
    margin: 0;
    position: absolute;
    text-align: center;
}

.container {
    background-color: black;
}
#navlinks :hover, #navlinks a:active, #navlinks a:focus, #navlinks a.thispage {
    color: violet;
}
#hero img {
    width:100%;
    display: flexbox;
}
#hero p {
    width: 100%;
    color: rgb(242, 221, 221);
    font-weight: 600;
    font-size: 1.5em;
    text-transform: uppercase;
    border-bottom-width: 3px;
    border-bottom-style: solid;
    border-top-style: solid;
    padding-left: 4%;
    padding-right: 4%;
    padding-bottom: 20px;
    margin-top: 10px;
}
.user-logo {
    border: none;
    border-radius: 50%;
    width: 20%;
    text-align: center;
    display: block;
    justify-content: center;
    align-items: center;
    margin: auto;
}

nav {
    background-color: black;
    display:flex;
    justify-content: space-around;
}

footer {
    padding-top: 20px;
    padding-bottom: 20px;
    background-color: black;
    color: white;
    font-size: 0.8em;
    text-align: center;
}

#navlinks {
    max-width: 1000px;
    position: static;
    background-color: transparent;
}
#navlinks a {
    width: 20%;
    padding-top: 15px;
    padding-bottom: 15px;
    margin-bottom: 20px;
    float: left;
    color: aqua;
}

#hero p {
    width: 36%;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 15px;
    padding-bottom: 15px;
    position: absolute;
    color: cyan;
    border-width: .20rem;
    border-style: solid;
    top: 3rem;
    right: 2rem;
}
#hero {
    width: 100%;
    height: 76vh;
    position: relative;
}


.content {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 30%;
    color: white;
}

main {
    width: 60%;
}
.row {
    background-color: black;
}
.contactme {
    height: 116vh;
    padding: 2rem;
}
.row-cols-1 {
    margin: 0.5rem;
    height: 100%;
}
div.col {
    margin-bottom: 1rem;
    max-height: 100%;
}
